/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(130, 40.5%, 52.5%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-weight: 500;
  color: var(--black);
  line-height: 1.3;
}
body.noscroll {
  overflow: hidden;
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
.row {
  padding: 0 1.25rem;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-display);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.btn__contact a, .btn__login a {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0.625rem 1.25rem;
  color: var(--nwcGreen);
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
@media (max-width: 59.999375em) {
  .btn__contact a, .btn__login a {
    margin-left: 0;
  }
}
.btn__contact a:hover, .btn__login a:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  transition: color 250ms ease-in-out;
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  background-color: var(--nwcGreen);
  display: flex;
  flex-direction: row;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 998;
}
.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(var(--nwcLightGreen), transparent);
}
.header__logo img {
  width: 10.9375rem;
  height: 5.3125rem;
}
.header__section {
  width: 100%;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--white);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0.625rem 0 0;
}
.header__nav--open {
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--nwcGreen);
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
  top: 125px;
  padding: 20px;
  z-index: 990;
  margin: 0 auto;
  transition: all 300ms ease-in-out;
}
@media (max-width: 59.999375em) {
  .header__nav--open {
    top: 6.875rem;
  }
}
.header__media {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__media {
    flex-direction: column;
  }
}
.header__search {
  margin-left: 1.875rem;
}
@media (max-width: 59.999375em) {
  .header__search {
    width: 100%;
    margin: 1.25rem 0 0;
  }
}
.header__search input[type=text] {
  font-size: 1rem;
  color: var(--lightGrey);
}
.header__search--placeholder {
  padding: 0.3125rem;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--newGreen);
}
@media (max-width: 59.999375em) {
  .header__search--placeholder {
    width: 100%;
  }
}
@media (max-width: 59.999375em) {
  .header__social {
    width: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__social a {
  margin-left: 1.25rem;
}
.header__social a:first-child {
  margin-left: 0;
}
.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  padding-bottom: 0.625rem;
}
@media (max-width: 59.999375em) {
  .header__menu {
    flex-direction: column;
  }
}
.header__menu ul {
  display: flex;
  gap: 1rem;
}
.header__menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--font-display);
  font-size: 1.0625rem;
  font-weight: 500;
  list-style: none;
}
@media (max-width: 59.999375em) {
  .header__menu li {
    margin: 0.625rem 0;
  }
}

@media (max-width: 59.999375em) {
  .head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: var(--nwcGreen);
  width: 100%;
}
@media (max-width: 59.999375em) {
  .nav {
    padding: 1.25rem;
  }
}
.nav__list {
  padding: 0;
  margin: 0;
}
.nav__list--open {
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-left: 0;
  transition: all 300ms ease-in-out;
}
.nav__item {
  margin-left: 1.25rem;
}
@media (max-width: 59.999375em) {
  .nav__list, .nav__item {
    flex-direction: column;
  }
}
.nav__list a, .nav__item a {
  color: var(--white);
  padding: 1rem 0;
  cursor: pointer;
}
.nav__list a:hover, .nav__item a:hover {
  color: var(--gold);
  transition: all 200ms ease-in-out;
}

.has-submenu {
  display: flex;
  flex-direction: column;
  position: relative;
}
.has-submenu ul.submenu {
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms ease-in-out;
}
.has-submenu:hover ul.submenu, .has-submenu:focus ul.submenu {
  opacity: 1;
  visibility: visible;
  display: flex;
  flex-direction: column;
}
.has-submenu:hover ul.submenu li.subitem, .has-submenu:focus ul.submenu li.subitem {
  padding-left: 0;
  margin: 0;
  width: 12rem;
  transition: background-color 200ms ease-in-out;
}

.submenu {
  justify-content: center;
  align-items: center;
  padding-left: 0;
  text-align: center;
  position: absolute;
  background-color: var(--nwcGreen);
  padding: 0;
  top: 2em;
  margin-top: 1em;
}
.submenu li {
  margin-left: 0;
}

.subitem {
  color: var(--white);
  width: 100%;
  margin: 0;
  padding: 0.5rem 0;
}
.subitem:hover, .subitem:focus {
  background-color: var(--nwcLightGreen);
}
.subitem:last-child {
  padding-bottom: 0.75em;
}
.subitem a {
  padding: 0;
  gap: 0;
}

@media (max-width: 59.999375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 60em) {
  .hide-for-desktop {
    display: none;
  }
}

.drop {
  display: flex;
  flex-direction: column;
  z-index: 999;
}

.hero__index {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  overflow: hidden;
  padding: 0;
  margin-bottom: 1.25rem;
}
@media (max-width: 59.999375em) {
  .hero__index {
    transition: all 350ms ease-in-out;
  }
}
.panels {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 25rem;
  overflow: hidden;
  position: relative;
  justify-content: center;
  align-items: center;
}
@media (max-width: 59.999375em) {
  .panels {
    flex-direction: column;
  }
}
.panels__halfWidth {
  margin-bottom: 1.25rem;
  height: auto;
}
.panels__halfWidth--conf h2, .panels__halfWidth--pharm h2 {
  font-family: var(--font-display);
  font-size: 3rem;
  letter-spacing: 0.125rem;
  color: var(--white);
  text-shadow: 0.1875rem 0.1875rem 0.3125rem var(--black);
}
.panels__halfWidth--conf, .panels__halfWidth--pharm {
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center top;
  width: 50%;
  height: 25rem;
}
@media (max-width: 59.999375em) {
  .panels__halfWidth--conf, .panels__halfWidth--pharm {
    width: 100%;
  }
}
.panels__halfWidth--conf {
  background-image: url("../img/panels_conference_1300x400-min.jpg");
}
.panels__halfWidth--pharm {
  background-image: url("../img/panels_pharmacy_1300x400-min.jpg");
}
@media (max-width: 59.999375em) {
  .panels__halfWidth--pharm {
    margin-top: 1.25rem;
  }
}
.panels__fullWidth {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../img/panels_tradeshow_1920x400-min.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  width: cover;
  height: 25rem;
}
.panels__fullWidth--trade h2 {
  font-family: var(--font-display);
  font-size: 3rem;
  letter-spacing: 0.125rem;
  color: var(--white);
  text-shadow: 0.1875rem 0.1875rem 0.3125rem var(--black);
}
.panels__fullBanner {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--nwcLightBlue);
  width: cover;
  margin-bottom: 1.25rem;
}
.panels__fullBanner h3 {
  font-family: var(--font-display);
  font-size: 1.4375rem;
  font-weight: 500;
  color: var(--white);
  margin-bottom: 0;
  padding: 1.25rem 0;
  text-align: center;
}

.wpgCaps {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}

.sponsors__banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--nwcGreen);
  padding: 1.25rem 0;
  color: var(--white);
}
.sponsors__banner h2 {
  font-size: 3rem;
  margin-bottom: 0.625rem;
}
.sponsors__banner h3 {
  font-size: 1.75rem;
  margin-bottom: 0;
}
.sponsors__norWester, .sponsors__furTrader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
  text-align: center;
  padding: 1.25rem 0;
}
.sponsors__norWester--logos img, .sponsors__furTrader--logos img {
  width: 80%;
}
.sponsors__norWester {
  flex-wrap: wrap;
  flex: 3;
}
.sponsors__norWester--logos {
  flex: 25%;
}
@media (max-width: 43.74875em) {
  .sponsors__norWester--logos {
    flex: 100%;
  }
}
.sponsors__furTrader {
  flex-wrap: wrap;
}
.sponsors__furTrader--logos {
  flex: 10%;
}
@media (max-width: 59.999375em) {
  .sponsors__furTrader--logos {
    flex: 20%;
  }
}
@media (max-width: 43.74875em) {
  .sponsors__furTrader--logos {
    flex: 35%;
  }
}

.card {
  padding: 1.25rem;
}
.card__button {
  width: 1rem;
  height: 1rem;
  border-radius: 0.9375rem;
  background-color: var(--nwcLightGreen);
}

.footer {
  background-color: var(--nwcGreen);
  display: flex;
  padding-top: 2.8125rem;
  padding-bottom: 1.25rem;
}
@media (max-width: 59.999375em) {
  .footer .container {
    width: 100%;
  }
}
.footer__nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.footer__nav--login {
  margin-left: 1.875rem;
}
@media (max-width: 59.999375em) {
  .footer__nav {
    flex-direction: column;
    width: 100%;
    margin: 0;
  }
}
@media (max-width: 43.74875em) {
  .footer__nav--conference, .footer__nav--tradeshow, .footer__nav--featureItems, .footer__nav--pharmacy, .footer__nav--storeawards, .footer__nav--contact {
    width: 50%;
    margin-bottom: 1.875rem;
  }
  .footer__nav--login {
    width: 100%;
    margin-left: 0;
  }
}
.footer__nav ul {
  margin-top: 0;
  padding-left: 0;
}
.footer__nav ul li {
  list-style: none;
  margin-top: 0.5rem;
}
.footer__logo img {
  width: 10.9375rem;
  height: 5.3125rem;
  margin-right: 2.5rem;
}
@media (max-width: 59.999375em) {
  .footer__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    text-align: center;
    margin-bottom: 40px;
  }
}
.footer__menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  flex: 2;
  align-items: flex-start;
  margin-bottom: 1.25rem;
}
.footer__menu a {
  color: var(--white);
  font-weight: 500;
  cursor: pointer;
}
.footer__menu a:hover {
  text-decoration: underline;
}
.footer__menu--head {
  font-family: var(--font-display);
  font-weight: 700;
  color: var(--white);
  font-size: 1.375rem;
  margin-bottom: 0;
}
.footer__media {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-top: 0.0625rem solid var(--white);
  padding-top: 1.25rem;
}
.footer__media p {
  font-size: 0.875rem;
  color: var(--white);
}
.footer__media--links a {
  margin-right: 1.25rem;
}
.footer__media--links a:hover {
  text-decoration: underline;
}
.footer__media a {
  color: var(--white);
}
.footer__media--social > a.twitter {
  margin-left: 1.25rem;
}